@import "../../global.scss";
.topbar {
  width: 100%;
  height: 75px;
  background-color: white;
  color: $mainColor;
  position: fixed;
  top: 0;
  z-index: 3;
  transition: all 1s ease;
  box-shadow: 1px 1px 4px 1px #e4e4e4;

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left {
      &::-webkit-scrollbar {
        display: none;
      }
      display: flex;
      align-items: center;
      img {
        width: 45px;
        padding-right: 1rem;
        float: left;
        transition: all 1s ease;
      }
      .logo {
        text-decoration: none;
        color: inherit;
        font-weight: 700;
        font-size: 40px;
        margin-right: 40px;
        @include mobile {
          font-size: 25px;
        }
      }
      .itemContainer {
        display: flex;
        align-items: center;
        margin-right: 30px;
        @include mobile {
          display: none;
        }
      }
      .icon {
        font-size: 18px;
        margin-right: 5px;
      }
      span {
        font-size: 15px;
        font-weight: 500;
      }
    }
    .right {
      .hamburger {
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        span {
          width: 90%;
          height: 3px;
          background-color: $mainColor;
          transform-origin: left;
          transition: all 2s ease;
        }
      }
    }
  }
  &.active {
    background-color: $mainColor;
    box-shadow: 1px 1px 4px 1px #000000;

    color: white;
    img {
      filter: brightness(0) invert(1);
    }
    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
